html {
  background-color: #d8d8d8;
}

body {
  margin: 0px;
}

.App {
  font-family: sans-serif;
  text-align: center;
}

header {
  display: flex;
  justify-content: space-between;
  background-color: #ffffff;
}

h1 {
  font-family: serif;
  padding: 0 35px;
}

p {
  text-align: justify;
}

p.overview {
  text-align: left;
  margin: 25px 0 50px;
}

.contents02 {
  background-color: #49ddb8;
  width: 100px;
  padding-top: 23px;
}

.main {
  padding: 0 35px;
  max-width: 1000px;
  margin: 0 auto;
}

.container {
  display: flex;
  gap: 30px 20px;
  justify-content: center;
}

.send {
  margin-top: 50px;
  display: block;
  width: 85px;
  margin-left: auto;
}
